<template>
<div id="app">

    <div id="nav">
        <section>
            <header>
                <nav>
                    <ul>
                        <li>
                            <router-link id="i" to="/grupos">Grupos</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/estudiantes">Estudiantes</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/areas">√Åreas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/evaluacion">Evaluaci√≥n</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/preguntas">Preguntas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/alternativa">Alternativa</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/reporte">Reportes</router-link>
                        </li>
                        <li>
                             <router-link id="i" to="/">Salir</router-link>
                        </li>
                        <!-- <li><router-link id="i" to="/registro">Registro</router-link></li> -->
                    </ul>

                </nav>
            </header>

        </section>
    </div>

    <div class="container">
        <h2>Preguntas</h2>

        <b-alert :show="dismissCountDown" dismissible :variant="mensaje.color" @dismissed="dismissCountDown=0" @dismiss-count-down="countDownChanged">
            {{mensaje.texto}}
        </b-alert>

        <form @submit.prevent="editarPregunta(preguntaEditar)" v-if="editar">
            <h4>Modificar Pregunta</h4>
            <div class="mb-3 row" style="width: 200px">
                <input type="text" class="form-control my-2" name="enunciado" value="" size="11" maxlength="50" placeholder="Enunciado" v-model="preguntaEditar.enunciado" required /><br>
                <input type="text" class="form-control my-2" name="descripcion" value="" size="50" maxlength="50" placeholder="Descripcion" v-model="preguntaEditar.descripcion" /><br>
                <select v-model="preguntaEditar.evaluacionId" class="form-control" id="pregunta">
                    <option disabled selected>Seleccione una Evaluacion</option>
                    <option v-for="evaluacion in evaluaciones" :key="evaluacion" :value="evaluacion._id">{{evaluacion.nombre}}</option>
                </select>

            </div>
            <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
            <b-button class="my-2" type="submit" @click="editar = false">Cancelar</b-button>

        </form>

        <form @submit.prevent="agregarPregunta()" hidden="true" v-if="!editar" id="form_1">
            <h4>Agregar Pregunta</h4>
            <div class="mb-3 row" style="width: 200px">
                <input type="text" class="form-control my-2" name="enunciado" value="" size="20" maxlength="50" placeholder="Enunciado" v-model="pregunta.enunciado" required />
                <input type="text" class="form-control my-2" name="descripcion" value="" size="50" maxlength="50" placeholder="Descripcion" v-model="pregunta.descripcion" />

                <select v-model="pregunta.evaluacionId" class="form-control" id="pregunta">
                    <option disabled selected>Seleccione una Evaluacion</option>
                    <option v-for="evaluacion in evaluaciones" :key="evaluacion" :value="evaluacion._id">{{evaluacion.nombre}}</option>
                </select>
            </div>
            <b-button class="btn-success my-2 btn-block" type="submit">Agregar</b-button>&nbsp;
            <b-button class="my-2" type="button" @click="agregarCancelar()">Cancelar</b-button>

        </form>
        <hr>
        <tr>
            <th>Evaluacion:</th> &nbsp;
            <th style="width: 200px;">
                <select v-model="filtroEvaluacion" class="form-control">
                    <option>Todos</option>
                    <option v-for="evaluacion in evaluaciones" :key="evaluacion" :value="evaluacion._id">{{evaluacion.nombre}}</option>
                </select>
            </th>&nbsp;

            <th>
                <b-button variant="info" @click="filtroEvaluaciones()" type="submit">Filtrar</b-button>
            </th>
        </tr>
        <br>
    <table class="table  table table-primary table-striped">
            <thead>
                <tr class="table-secondary">
                    <th scope="col">id</th>
                    <th scope="col">Enunciado</th>
                    <th scope="col">Descripci√≥n</th>
                    <th scope="col">
                        <a href="#" @click="agregar()" title="Adicionar">
                            <img src="../assets/a√±adir.png" />
                    </a>
                        <!-- <b-button class="btn-success my-2 btn-block" title="Adicionar" type="button" @click="agregar()">
                            <img src="../assets/a√±adir.png" @click="agregar()" />
                        </b-button> -->
                    </th>

                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in preguntas" :key="index">
                    <th scope="row">{{ item._id }}</th>
                    <td>{{ item.enunciado }}</td>
                    <td>{{item.descripcion}}</td>
                    <td>
                        <!-- <b-button @click="alerta()">Acci√≥n</b-button> -->
                        <b-button @click="eliminarPregunta(item._id)" class="btn-danger btn-sm mx-2">Eliminar
                        </b-button>
                        <b-button @click="activarEdicion(item._id)" class="btn-warning btn-sm">Editar
                        </b-button>
                        <!-- <b-button  class="btn-warning btn-sm">Editar
                    </b-button> -->
                    </td>

                </tr>

            </tbody>
        </table>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            preguntas: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {
                color: "",
                texto: ""
            },
            pregunta: {
                enunciado: "",
                descripcion: "",
                evaluacionId: ""
            },
            evaluaciones: [],
            evaluacion: {
                nombreG: ""

            },
            editar: false,
            preguntaEditar: {},
            filtroEvaluacion: "Todos",
        };
    },
    created() {
        this.listarPreguntas();
        this.listarEvaluaciones();
    },
    methods: {
         filtroEvaluaciones() {
            var criterio2 = this.filtroEvaluacion

            if (criterio2 === "Todos") {

                this.axios.get('/pregunta')
                    .then((response) => {
                        this.preguntas = response.data;
                    })
                    .catch((e) => {
                        console.log('error' + e);
                    })

            } else {

                this.axios.get(`/filtroP/${criterio2}`)

                    .then((response) => {
                        console.log(response.data)
                        this.preguntas = response.data;
                    })
                    .catch((e) => {
                        console.log('error' + e);
                    })

            }

        },
        listarPreguntas() {
            this.axios.get('/pregunta')
                .then(res => {
                    console.log(res.data);
                    this.preguntas = res.data;
                    console.log(preguntas);
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },
        listarEvaluaciones() {
            this.axios.get('/evaluacion')
                .then(res => {
                    console.log(res.data);
                    this.evaluaciones = res.data;
                    console.log(evaluaciones);
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },
        agregar() {
            document.getElementById('form_1').hidden = false

        },
        agregarCancelar() {
            document.getElementById('form_1').hidden = true
            this.pregunta.enunciado = '';
            this.pregunta.descripcion = '';
            this.pregunta.evaluacionId = '';
        },
        agregarPregunta() {
            this.axios.post('/nueva-pregunta', this.pregunta)
                .then(res => {
                    this.preguntas.push(res.data)
                    this.pregunta.enunciado = '';
                    this.pregunta.descripcion = '';
                    this.pregunta.evaluacionId = '';

                    this.mensaje.color = 'success';
                    this.mensaje.texto = 'Pregunta Agregada!';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true
                })
                .catch(e => {
                    console.log(e.response);
                    if (e.response.data.error.errors.nombre.message) {
                        this.mensaje.texto = e.response.data.error.errors.nombre.message
                    } else {
                        this.mensaje.texto = 'Error de sistema';
                    }
                    this.mensaje.color = 'danger';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true

                })

        },
        eliminarPregunta(id) {
            console.log(id);
            this.axios.delete(`/pregunta/${id}`)
                .then(res => {
                    const index = this.preguntas.findIndex(item => item._id === res.data._id);
                    this.preguntas.splice(index, 1);
                    this.mensaje.color = 'danger';
                    this.mensaje.texto = 'Pregunta Eliminada';
                    this.showAlert();
                })
                .catch(e => {
                    console.log(e.response);
                })

        },
        activarEdicion(id) {
            this.editar = true;
            console.log(id);
            this.axios.get(`/pregunta/${id}`)
                .then(res => {
                    this.preguntaEditar = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        editarPregunta(item) {
            this.axios.put(`/pregunta/${item._id}`, item)
                .then(res => {
                    const index = this.preguntas.findIndex(n => n._id === res.data._id);
                    this.preguntas[index].enunciado = res.data.enunciado;
                    this.preguntas[index].descripcion = res.data.descripcion;
                    this.preguntas[index].evaluacionId = res.data.evaluacionId;

                    this.mensaje.color = 'success';
                    this.mensaje.texto = 'Pregunta Editada';
                    this.showAlert();
                    this.editar = false;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs
        }
    },
}
</script>

<style>
img {
    width: 35px;
}

h2 {
    padding: 35px;
    text-align: center;
}
</style>

<style>
* {
    text-decoration: none;
    list-style: none;
    margin: 0px;
    padding: 0px;
    outline: none;
}

body {
    margin: 0px;
    padding: 0px;
    font-family: 'Open Sans', sans-serif;
}

section {
    width: 100%;
    max-width: 1400px;
    display: table;
    position: relative;
}

/* h1{margin:0px auto; display:table; font-size:26px; padding:40px 0px; color:#002e5b; text-align:center;}
h1 span{font-weight:500;} */

header {
    width: 100%;
    display: table;
    background-color: rgb(13, 65, 47);
    margin-bottom: 20px;
}

nav {
    width: auto;
    float: right;
}

nav ul {
    display: table;
    float: right;
}

nav ul li {
    float: left;
}

nav ul li:last-child {
    padding-right: 0px;
}

nav ul li a {
    color: rgb(167, 241, 215);
    font-size: 16px;
    padding: 18px 20px;
    display: inline-block;
    transition: all 0.5s ease 0s;
}

nav ul li a:hover {
    background-color: rgb(167, 241, 215);
    color: rgb(13, 65, 47);
    transition: all 0.5s ease 0s;
}

nav ul li a:hover i {
    color: #fde428;
    transition: all 0.5s ease 0s;
}

#i {
    text-decoration: none;
}
</style><style lang="scss">
#nav {
    padding: 0px;
    height: 60px;

    a {
        font-weight: bold;

        &.router-link-exact-active {
            color: #5379fa;

        }
    }
}
</style>
