<template>
<div class="about">
    <div class="body"></div>
    <div class="container">
   
    <b-alert :show="dismissCountDown" dismissible :variant="mensaje.color" @dismissed="dismissCountDown = 0" @dismiss-count-down="countDownChanged">
        {{ mensaje.texto }}
    </b-alert>
    </div>
    <div class="grad"></div>
    <div class="header">
         
        <div>Registro Evalua<span>Tech</span></div>
    </div>
    <br />
    <div class="login">
        <form @submit.prevent="agregarDocente()">
            <input type="text" name="nombre" value="" size="50" maxlength="50" placeholder="Nombre del docente" v-model="docente.nombre" required />
            <input type="text" name="apellido" value="" size="50" maxlength="50" placeholder="Apellido del docente" v-model="docente.apellido" required />
            <input type="text" name="celular" value="" size="10" maxlength="10" placeholder="Celular del docente" v-model="docente.celular" required />
            <input type="text" name="usuario" value="" size="11" maxlength="11" placeholder="Identificaci√≥n del docente" v-model="docente.usuario" required />
            <input type="password" placeholder="Clave del docente" name="password" v-model="docente.clave" required />
            <input type="submit" value="Registrarse" />
            <router-link id="i" to="/"><button type="button">Volver</button></router-link>
        </form>
         
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            docentes: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {
                color: "",
                texto: ""
            },
            docente: {
                nombre: "",
                apellido: "",
                celular: "",
                usuario: "",
                clave: "",
            },
        };
    },

    methods: {
        alerta() {
            this.mensaje.color = "danger";
            this.mensaje.texto = "Probando alerta";
            this.showAlert();
        },

        lista_docentes() {

            this.axios
                .get("/docente")
                .then((res) => {
                    this.docentes = res.data;

                })
                .catch((e) => {
                    console.log(e.response);
                    this.showAlert();
                });

        },

        agregarDocente() {
            let valido = true;
            this.lista_docentes();
            setTimeout(() => {
                for (let i = 0; i < this.docentes.length; i++) {
                    console.log(this.docentes[i].usuario);
                    if (this.docentes[i].usuario === this.docente.usuario) {
                        valido = false;
                    }
                }

                if (valido) {
                    this.axios
                        .post("/nuevo-docente", this.docente)
                        .then((res) => {
                            this.docentes.push(res.data);
                            this.docente.nombre = "";
                            this.docente.apellido = "";
                            this.docente.celular = "";
                            this.docente.usuario = "";
                            this.docente.clave = "";
                            //Limpieza de los campos
                            this.mensaje.color = "success";
                            this.mensaje.texto = "¬°Registro exitoso!";
                            this.showAlert();
                        })
                        .catch((e) => {
                            console.log(e.response);
                            if (e.response.data.error.errors.nombre.message) {
                                this.mensaje.texto =
                                    e.response.data.error.errors.nombre.message;
                            } else {
                                this.mensaje.texto = "Error de sistema";
                            }
                            this.mensaje.color = "danger";
                            this.showAlert();
                        });
                } else {
                    this.docente.nombre = "";
                    this.docente.apellido = "";
                    this.docente.celular = "";
                    this.docente.usuario = "";
                    this.docente.clave = "";
                    this.mensaje.color = "danger";
                    this.mensaje.texto = "Nombre de usuario en uso. Docente no registrado";
                    //alert("Nombre de usuario en uso");
                    this.showAlert();
                }
            }, 1000);

            //}
        },

        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown;
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs;
        },
    },
};
</script>

<style scoped>
@import url(https://fonts.googleapis.com/css?family=Exo:100,200,400);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:700,400,300);

