    background: -webkit-gradient(linear,
            left top,
            left bottom,
            color- stop(0%, rgba(0, 0, 0, 0)),
            color- stop(100%, rgba(0, 0, 0, 0.65)));
    /* Chrome,Safari4+ */
    z-index: 1;
    opacity: 0.7;
}

.header {
    position: absolute;
    top: calc(50% - 35px);
    left: calc(50% - 395px);
    z-index: 2;
}

.header div {
    
    
    color: #fff;
    font-family: "Exo", sans-serif;
    font-size: 35px;
    font-weight: 300;
}


.header div span {
    color: #5379fa !important;
}

.login {
    position: absolute;
    /* top: calc(50% - 75px); */
    top: calc(39% - 75px);
    left: calc(50% - 50px);
    height: 450px;
    width: 350px;
    padding: 10px;
    z-index: 2;
}

.login input[type="text"] {
    width: 250px;
    height: 30px;
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.6);
    border-radius: 2px;
    color: #fff;
    font-family: "Exo", sans-serif;
    font-size: 16px;
    font-weight: 400;
    padding: 4px;
    margin-top: 10px;
}

.login input[type="email"] {
    width: 250px;
    height: 30px;
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.6);
    border-radius: 2px;
    color: #fff;
    font-family: "Exo", sans-serif;
    font-size: 16px;
    font-weight: 400;
    padding: 4px;
    margin-top: 10px;
}

.login input[type="password"] {
    width: 250px;
    height: 30px;
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.6);
    border-radius: 2px;
    color: #fff;
    font-family: "Exo", sans-serif;
    font-size: 16px;
    font-weight: 400;
    padding: 4px;
    margin-top: 10px;
}

.login input[type="submit"] {
    width: 260px;
    height: 35px;
