<template>
<div id="app">

    <div id="nav">
        <section>
            <header>
                <nav>
                    <ul>
                        <li>
                            <router-link id="i" to="/grupos">Grupos</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/estudiantes">Estudiantes</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/areas">√Åreas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/evaluacion">Evaluaci√≥n</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/preguntas">Preguntas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/alternativa">Alternativa</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/reporte">Reportes</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/">Salir</router-link>
                        </li>

                    </ul>

                </nav>
            </header>

        </section>
    </div>

    <div class="container">
        <h2>Estudiantes</h2>
        <b-alert :show="dismissCountDown" dismissible :variant="mensaje.color" @dismissed="dismissCountDown=0" @dismiss-count-down="countDownChanged">
            {{mensaje.texto}}
        </b-alert>
        <form @submit.prevent="editarEstudiante(estudianteEditar)" v-if="editar">
            <h4>Modificar Estudiante</h4>
            <div class="mb-3 row" style="width: 200px">
                <input type="text" class="form-control my-2" name="identificacion" value="" size="11" maxlength="11" placeholder="Identificaci√≥n" v-model="estudianteEditar.identificacion" required /><br>
                <input type="text" class="form-control my-2" name="nombre" value="" size="50" maxlength="50" placeholder="Nombre" v-model="estudianteEditar.nombre" required /><br>
                <input type="text" class="form-control my-2" name="apellido" value="" size="50" maxlength="50" placeholder="Apellido" v-model="estudianteEditar.apellido" required /><br>
                <input type="text" class="form-control my-2" name="celular" value="" size="10" maxlength="10" placeholder="Celular" v-model="estudianteEditar.celular" required /><br>
                <input type="password" class="form-control my-2" placeholder="Clave" name="password" v-model="estudianteEditar.clave" required />
                <select v-model="estudianteEditar.grupoId" class="form-control" id="estudiante">
                    <option v-for="grupo in grupos" :key="grupo" :value="grupo._id">{{grupo.nombre}}</option>
                </select>

            </div>
            <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
            <b-button class="my-2" type="submit" @click="editar = false">Cancelar</b-button>
        </form>

        <form @submit.prevent="agregarEstudiante()" v-if="!editar" hidden="true" id="form_1">
            <h4>Agregar Estudiante</h4>
            <div class="mb-3 row" style="width: 200px">
                <input type="text" class="form-control my-2" name="identificacion" value="" size="11" maxlength="11" placeholder="Identificaci√≥n" v-model="estudiante.identificacion" required />
                <input type="text" class="form-control my-2" name="nombre" value="" size="50" maxlength="50" placeholder="Nombre" v-model="estudiante.nombre" required />
                <input type="text" class="form-control my-2" name="apellido" value="" size="50" maxlength="50" placeholder="Apellido" v-model="estudiante.apellido" required />
                <input type="text" class="form-control my-2" name="celular" value="" size="10" maxlength="10" placeholder="Celular" v-model="estudiante.celular" required />
                <input type="password" class="form-control my-2" placeholder="Clave" name="password" v-model="estudiante.clave" required />
                <select v-model="estudiante.grupoId" class="form-control" id="estudiante">
                    <option v-for="grupo in grupos" :key="grupo" :value="grupo._id">{{grupo.nombre}}</option>
                </select>

            </div>
            <b-button class="btn-success my-2 btn-block" type="submit">Agregar</b-button>&nbsp;
            <b-button class="my-2" type="button" @click="agregarCancelar()">Cancelar</b-button>

        </form>
        <hr>
        <tr>
            <th>Grupo:</th> &nbsp;
            <th style="width: 200px;">
                <select v-model="filtroGrupo" class="form-control" id="estudiante">
                    <option>Todos</option>
                    <option v-for="grupo in grupos" :key="grupo" :value="grupo._id">{{grupo.nombre}}</option>
                </select>
            </th>&nbsp;

            <th>
                <b-button variant="info" @click="filtroGrupos()" type="submit">Filtrar</b-button>
            </th>
        </tr>
        <br>
        <table class="table table table-primary table-striped">
            <thead>
                <tr class="table-secondary">
                    <th scope="col">Identificaci√≥n</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Apellido</th>
                    <th scope="col">Tel√©fono</th>
                    <th scope="col">Id Grupo</th>
                    <th scope="col">
                        <a href="#" @click="agregar()" title="Adicionar">
                            <img src="../assets/a√±adir.png" />
                        </a>
                        <!-- <b-button class="" title="Adicionar" type="button" @click="agregar()">
                            <img src="../assets/a√±adir.png" />
                        </b-button> -->
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in estudiantes" :key="index">
                    <th scope="row">{{ item.identificacion }}</th>
                    <td>{{ item.nombre }}</td>
                    <td>{{item.apellido}}</td>
                    <td>{{item.celular}}</td>
                    <td>{{item.grupoId}}</td>
                    <td>
                        <!-- <b-button @click="alerta()">Acci√≥n</b-button> -->
                        <b-button @click="eliminarEstudiante(item._id)" class="btn-danger btn-sm mx-2">Eliminar
                        </b-button>
                        <b-button @click="activarEdicion(item._id)" class="btn-warning btn-sm">Editar
                        </b-button>
                    </td>

                </tr>
            </tbody>
        </table>
    </div>
</div>
</template>

<

<script>
export default {
    data() {
        return {
            estudiantes: [],

            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {
                color: "",
                texto: ""
            },
            estudiante: {
                identificacion: "",
                nombre: "",
                apellido: "",
                celular: "",
                clave: "",
                grupoId: ""
            },
            grupos: [],
            grupo: {
                nombreG: ""

            },
            grupoEditar: {},
            editar: false,
            estudianteEditar: {},
            filtroGrupo: "Todos",
        };
    },
    created() {
        this.listarEstudiantes();
        this.listarGrupos();
        //this.filtroGrupo();
    },

    methods: {

        filtroGrupos() {
            var criterio2 = this.filtroGrupo

            if (criterio2 === "Todos") {

                this.axios.get('/estudiante')
                    .then((response) => {
                        // console.log(response.data)
                        this.estudiantes = response.data;
                    })
                    .catch((e) => {
                        console.log('error' + e);
                    })

            } else {

                this.axios.get(`/filtro/${criterio2}`)

                    .then((response) => {
                        console.log(response.data)

                        this.estudiantes = response.data;
                    })
                    .catch((e) => {
                        console.log('error' + e);
                    })

            }

        },

        listarGrupos() {
            this.axios.get('/grupo')
                .then(res => {
                    console.log(res.data);
                    this.grupos = res.data;
                    console.log(grupos);
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },
        listarEstudiantes() {
            this.axios.get('/estudiante')
                .then(res => {
                    console.log(res.data);
                    this.estudiantes = res.data;
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },

        agregar() {
            document.getElementById('form_1').hidden = false

        },
        agregarCancelar() {
            document.getElementById('form_1').hidden = true
            this.estudiante.identificacion = '';
            this.estudiante.nombre = '';
            this.estudiante.apellido = '';
            this.estudiante.celular = '';
            this.estudiante.clave = '';
            this.estudiante.grupoId = '';

        },

        agregarEstudiante() {
            this.axios.post('/nuevo-estudiante', this.estudiante)
                .then(res => {
                    this.grupos.push(res.data)
                    this.estudiante.identificacion = '';
                    this.estudiante.nombre = '';
                    this.estudiante.apellido = '';
                    this.estudiante.celular = '';
                    this.estudiante.clave = '';
                    this.estudiante.grupoId = '';

                    this.mensaje.color = 'success';
                    this.mensaje.texto = '¬°Estudiante Agregado!';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true
                    this.listarEstudiantes();
                })
                .catch(e => {
                    console.log(e.response);
                    if (e.response.data.error.errors.nombre.message) {
                        this.mensaje.texto = e.response.data.error.errors.nombre.message
                    } else {
                        this.mensaje.texto = 'Error de sistema';
                    }
                    this.mensaje.color = 'danger';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true
                    this.listarEstudiantes();

                })

        },
        eliminarEstudiante(id) {
            console.log(id);
            this.axios.delete(`/estudiante/${id}`)
                .then(res => {
                    const index = this.grupos.findIndex(item => item._id === res.data._id);
                    this.grupos.splice(index, 1);
                    this.mensaje.color = 'danger';
                    this.mensaje.texto = 'Estudiante Eliminado';
                    this.showAlert();
                    this.listarEstudiantes();
                })
                .catch(e => {
                    console.log(e.response);
                    this.listarEstudiantes();
                })

        },
        activarEdicion(id) {
            this.editar = true;
            console.log(id);
            this.axios.get(`/estudiante/${id}`)
                .then(res => {
                    this.estudianteEditar = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        activarGrupo() {
            this.editar = true;

            this.axios.get(`/grupo`)
                .then(res => {
                    this.grupoEditar = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        editarEstudiante(item) {
            this.axios.put(`/estudiante/${item._id}`, item)
                .then(res => {
                    const index = this.estudiantes.findIndex(n => n._id === res.data._id);
                    this.estudiantes[index].identificacion = res.data.identificacion;
                    this.estudiantes[index].nombre = res.data.nombre;
                    this.estudiantes[index].apellido = res.data.apellido;
                    this.estudiantes[index].celular = res.data.celular;
                    this.estudiantes[index].clave = res.data.clave;
                    this.estudiantes[index].grupoId = res.data.grupoId;

                    this.mensaje.color = 'success';
                    this.mensaje.texto = '¬°Estudiante Editado!';
                    this.showAlert();
                    this.editar = false;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs
        }
    },

}
</script>

