<template>
<div id="app">

    <div id="nav">
        <section>
            <header>
                <nav>
                    <ul>
                        <li>
                            <router-link id="i" to="/grupos">Grupos</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/estudiantes">Estudiantes</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/areas">√Åreas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/evaluacion">Evaluaci√≥n</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/preguntas">Preguntas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/alternativa">Alternativa</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/reporte">Reportes</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/">Salir</router-link>
                        </li>
                        <!-- <li><router-link id="i" to="/registro">Registro</router-link></li> -->
                    </ul>

                </nav>
            </header>

        </section>
    </div>
    <div class="container">
        <h2>Grupos</h2>
        <b-alert :show="dismissCountDown" dismissible :variant="mensaje.color" @dismissed="dismissCountDown = 0" @dismiss-count-down="countDownChanged">
            {{ mensaje.texto }}
        </b-alert>

        <form @submit.prevent="editarGrupo(grupoEditar)" v-if="editar">
            <h3>Editar grupo</h3>
            <input type="text" class="form-control my-2" placeholder="Nombre" v-model="grupoEditar.nombre" />
            <!-- <input type="text" class="form-control my-2" placeholder="Descripci√≥n" v-model="grupoEditar.descripcion"> -->
            <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
            <b-button class="my-2" type="submit" @click="editar = false">Cancelar</b-button>
        </form>

        <form @submit.prevent="agregarGrupo()" v-if="!editar" hidden="true" id="form_1">
            <h3>Agregar nuevo grupo</h3>
            <input type="text" class="form-control my-2" placeholder="Nombre" v-model="grupo.nombre" />
            <!-- <input type="text" class="form-control my-2" placeholder="Descripci√≥n" v-model="grupo.descripcion"> -->
            <b-button class="btn-success my-2 btn-block" type="submit">Agregar
            </b-button>&nbsp;
            <b-button class="my-2" type="button" @click="agregarCancelar()">Cancelar                
            </b-button>
        </form>

        <table class="table  table table-primary table-striped">
            <thead>
                <tr class="table-secondary">
                    <th scope="col">C√≥digo</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">
                      <a href="#" @click="agregar()" title="Adicionar">
                            <img src="../assets/a√±adir.png" />
                        </a>
                        <!-- <b-button class="btn-success my-2 btn-block" type="button" @click="agregar()">
                            <img src="../assets/a√±adir.png" />
                        </b-button> -->
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in grupos" :key="index">
                    <th scope="row">{{ item._id }}</th>
                    <td>{{ item.nombre }}</td>
                    <!-- <td>{{item.descripcion}}</td> -->
                    <td>
                        <!-- <b-button @click="alerta()">Acci√≥n</b-button> -->
                        <b-button @click="eliminarGrupo(item._id)" class="btn-danger btn-sm mx-2">Eliminar
                        </b-button>
                        <b-button @click="activarEdicion(item._id)" class="btn-warning btn-sm">Editar
                        </b-button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            grupos: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {
                color: "",
                texto: ""
            },
            grupo: {
                nombre: ""
            },
            editar: false,
            grupoEditar: {},
        };
    },

    created() {

        this.listarGrupos();

    },

    methods: {
        alerta() {
            this.mensaje.color = "danger";
            this.mensaje.texto = "Probando alerta";
            this.showAlert();
        },
        listarGrupos() {
            this.axios.get('/grupo')
                .then(res => {
                    console.log(res.data);
                    this.grupos = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        agregar() {
            document.getElementById('form_1').hidden = false

        },
        agregarCancelar() {
            document.getElementById('form_1').hidden = true
            this.grupo.nombre = '';

        },

        agregarGrupo() {

            // console.log(this.nota);
            this.axios.post('/nuevo-grupo', this.grupo)
                .then(res => {
                    this.grupos.push(res.data)
                    this.grupo.nombre = '';

                    //Limpieza de los campos
                    this.mensaje.color = 'success';
                    this.mensaje.texto = '¬°Grupo Agregado!';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true
                })
                .catch(e => {
                    console.log(e.response);
                    if (e.response.data.error.errors.nombre.message) {
                        this.mensaje.texto = e.response.data.error.errors.nombre.message
                    } else {
                        this.mensaje.texto = 'Error de sistema';
                    }
                    this.mensaje.color = 'danger';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true

                })
        },
        eliminarGrupo(id) {
            console.log(id);
            this.axios.delete(`/grupo/${id}`)
                .then(res => {
                    const index = this.grupos.findIndex(item => item._id === res.data._id);
                    this.grupos.splice(index, 1);
                    this.mensaje.color = 'danger';
                    this.mensaje.texto = 'Grupo Eliminado';
                    this.showAlert();
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        activarEdicion(id) {
            this.editar = true;
            console.log(id);
            this.axios.get(`/grupo/${id}`)
                .then(res => {
                    this.grupoEditar = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        editarGrupo(item) {
            this.axios.put(`/grupo/${item._id}`, item)
                .then(res => {
                    const index = this.grupos.findIndex(n => n._id === res.data._id);
                    this.grupos[index].nombre = res.data.nombre;

                    this.mensaje.color = 'success';
                    this.mensaje.texto = 'Grupo Editado';
                    this.showAlert();
                    this.editar = false;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs
        }
    },
};
</script>

<style>
img {
    width: 35px;
}

h2 {
    padding: 35px;
    text-align: center;
}
</style>

<style>
* {
    text-decoration: none;
    list-style: none;
    margin: 0px;
    padding: 0px;
    outline: none;
}

body {
    margin: 0px;
    padding: 0px;
    font-family: 'Open Sans', sans-serif;
}

section {
    width: 100%;
    max-width: 1400px;
    display: table;
    position: relative;
}

/* h1{margin:0px auto; display:table; font-size:26px; padding:40px 0px; color:#002e5b; text-align:center;}
h1 span{font-weight:500;} */

header {
    width: 100%;
    display: table;
    background-color: rgb(13, 65, 47);
    margin-bottom: 20px;
}

nav {
    width: auto;
    float: right;
}

nav ul {
    display: table;
    float: right;
}

nav ul li {
    float: left;
}

nav ul li:last-child {
    padding-right: 0px;
}

nav ul li a {
    color: rgb(167, 241, 215);
    font-size: 16px;
    padding: 18px 20px;
    display: inline-block;
    transition: all 0.5s ease 0s;
}

nav ul li a:hover {
    background-color: rgb(167, 241, 215);
    color: rgb(13, 65, 47);
    transition: all 0.5s ease 0s;
}

nav ul li a:hover i {
    color: #fde428;
    transition: all 0.5s ease 0s;
}

#i {
    text-decoration: none;
}
</style><style lang="scss">
#nav {
    padding: 0px;
    height: 60px;

    a {
        font-weight: bold;

        &.router-link-exact-active {
            color: #5379fa;

        }
    }
}
</style>
