<template>
<div id="app">

    <div id="nav">
        <section>
            <header>
                <nav>
                    <ul>
                        <li>
                            <router-link id="i" to="/evaluacionE">Evaluacion</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/resultado">Resultados</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/">Salir</router-link>
                        </li>

                        <!-- <li><router-link id="i" to="/registro">Registro</router-link></li> -->
                    </ul>
                </nav>
            </header>
        </section>
    </div>
    <div class="container">

        <div>
            <br><br>
            <h1 style="text-align: center">Sus evaluaciones</h1>
            <!--Alerta de evaluacion realizada satisfacctoriamente-->
            <b-alert :show="dismissCountDown" dismissible :variant="mensaje.color" @dismissed="dismissCountDown = 0" @dismiss-count-down="countDownChanged">
                {{ mensaje.texto }}
            </b-alert>
            <!--Modal de la nota-->
            <div class="container mt-5">
                <div class="modalVue">
                    <b-button style="float: right" variant="success" hidden=true id="modal" v-b-modal.modal-1>Ver Nota</b-button>

                    <b-modal id="modal-1">
                        <!-- <p class="my-4" ></p> -->
                        <h2>Su nota es: {{mensaje.notaF}}</h2>
                    </b-modal>
                </div>
            </div>
            <h3>Seleccione Evaluacion</h3>
            <select id="evalua" v-model="evaluacion.nombre" @click="cargarEva()" @change="cargarPreg()" class="form-control">
                <option value="0" selected>Seleccione evaluacion</option>
            </select>
        </div>
        <form @submit.prevent="calcularNota()">
            <div id="contenido"></div>
            <div>
                <br />
                <b-button style="float:none" pill variant="success" id="btn-enviar" class="btn-success my-2 btn-block" type="submit" hidden="true">
                    Enviar
                </b-button>
            </div>
        </form>
        <!-- <input type="text" class="form-control my-2" placeholder="Descripci√≥n" v-model="grupo.descripcion"> -->
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            estudiantes: [],
            evaluaciones: [],
            evaluacionesAux: [],
            preguntas: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {
                color: "",
                texto: "",
                notaF: 0
            },
            evaluacion: {
                nombre: "",
                realizada: false,
            },
        };
    },

    created() {
        this.listarEvaluaciones();
        this.listarEstudiantes();
        this.listarPreguntas();
        //alert(window.location.href);
        //alert(this.$route.path);
        //alert(this.$route.params);
        //console.log(this.$route.params);
        // window.addEventListener("load", this.cargarEva());
        //this.cargarEva();
    },

    methods: {
        alerta() {
            this.mensaje.color = "danger";
            this.mensaje.texto = "Probando alerta";
            this.showAlert();
        },
        listarEvaluaciones() {
            this.axios
                .get("/evaluacion")
                .then((res) => {
                    console.log(res.data);
                    this.evaluaciones = res.data;
                    this.evaluacionesAux = res.data;
                })

                .catch((e) => {
                    console.log(e.response);
                });
        },
        listarEstudiantes() {
            this.axios
                .get("/estudiante")
                .then((res) => {
                    console.log(res.data);
                    this.estudiantes = res.data;
                })
                .catch((e) => {
                    console.log(e.response);
                });
        },
        listarPreguntas() {
            this.axios
                .get("/pregunta")
                .then((res) => {
                    console.log(res.data);
                    this.preguntas = res.data;
                })
                .catch((e) => {
                    console.log(e.response);
                });
        },
        // modificarEvaluacion(sel, realiza) {

        //     let item = {};
        //     for (let j = 0; j < this.evaluacionesAux.length; j++) {
        //         if (this.evaluacionesAux[j]._id === sel) {
        //             item = this.evaluacionesAux[j];
        //         }
        //     }

        //     item.realizada = realiza;

        //     console.log(item);
        //     let pregun = item.preguntas;
        //     item = {
        //         areaId: item.areaId,
        //         nombre: item.nombre,
        //         realizada: item.realizada,
        //         fecha: item.fecha,
        //         _id: item._id,
        //         descripcion: item.descripcion,
        //         grupoId: item.grupoId
        //     }

        //     this.axios
        //         .put(`/evaluacion/${item._id}`, item)
        //         .then((res) => {

        //             res.data.realizada = item.realizada;

        //             console.log(res.data);
        //         })
        //         .catch((e) => {
        //             console.log(e.response);
        //         });
        // },

        cargarEva() {
            let opts = document.getElementById("evalua");

            // let option = document.createElement("option");
            // option.setAttribute("value", "0");
            // option.appendChild(document.createTextNode("Seleccione una evaluacion"));
            // opts.appendChild(option);
            // this.listarEvaluaciones();
            // this.listarEstudiantes();

            //Este Id corresponde al del estudiante que ingresa
            let idEs = localStorage.getItem("idEst");
            //alert(localStorage.getItem("idEst"));

            for (let i = 0; i < this.estudiantes.length; i++) {

                for (let j = 0; j < this.evaluaciones.length; j++) {

                    for (let k = 0; k < this.evaluaciones[j].preguntas.length; k++) {

                        if (this.evaluaciones[j].preguntas[k].alternativas.length <= 1 || this.evaluaciones[j].preguntas.length === 0) {
                            //  alert(this.evaluaciones[j].preguntas[k].alternativas.length);
                            continue;
                        } else {

                            if (
                                this.evaluaciones[j].grupoId === this.estudiantes[i].grupoId &&
                                idEs === this.estudiantes[i]._id &&
                                this.evaluaciones[j].realizada === false
                            ) {

                                let option = document.createElement("option");
                                option.setAttribute("value", this.evaluaciones[j]._id);
                                option.appendChild(
                                    document.createTextNode(this.evaluaciones[j].nombre)
                                );
                                opts.appendChild(option);
                                break;
                            }
                        }
                    }
                    // if (this.evaluaciones[j].preguntas.length === 0) {
                    //     continue;
                    // } else {
                    //     if (
                    //         this.evaluaciones[j].grupoId === this.estudiantes[i].grupoId &&
                    //         idEs === this.estudiantes[i]._id &&
                    //         this.evaluaciones[j].realizada === false
                    //     ) {

                    //         let option = document.createElement("option");
                    //         option.setAttribute("value", this.evaluaciones[j]._id);
                    //         option.appendChild(
                    //             document.createTextNode(this.evaluaciones[j].nombre)
                    //         );
                    //         opts.appendChild(option);
                    //     }
                    // }

                }
            }
            this.evaluaciones = "";
            this.estudiantes = "";
        },

        cargarPreg() {
            // this.listarEvaluaciones();
            // this.listarEstudiantes();
            // this.listarPreguntas();
            let sel = document.getElementById("evalua").value;
            let container = document.querySelector("#contenido");
            container.innerHTML = "";
            document.getElementById("btn-enviar").hidden = true;
            for (let i = 0; i < this.preguntas.length; i++) {
                if (this.preguntas[i].evaluacionId === sel) {

                    if (this.preguntas[i].alternativas.length === 0) {
                        let myH3 = document.createElement('h3');
                        myH3.innerHTML = "<br>Evaluacion en proceso";
                        container.appendChild(myH3);
                    } else {
                        let myDivs = document.createElement("div");
                        myDivs.id = "div" + i;
                        myDivs.innerHTML = "<br><hr><h4>" + this.preguntas[i].enunciado + "</h4>";

                        container.appendChild(myDivs);
                        for (let j = 0; j < this.preguntas[i].alternativas.length; j++) {
                            let container2 = document.getElementById('div' + i);
                            let myDiv = document.createElement("div");
                            myDiv.innerHTML = "<input type=\"radio\" class=\"form-check-input\" id=\"uno\" name=respu" + i + " value=" + this.preguntas[i].alternativas[j].valor + " required><label for=\"uno\" class=\"form-check-label\">" + this.preguntas[i].alternativas[j].respuesta + "</label><br>";
                            // "<br><h3>" +
                            // this.preguntas[i].enunciado +
                            // '</h3><input type="radio" class="form-check-input" id="uno" name=respu' +
                            // i +
                            // " value=" +
                            // this.preguntas[i].alternativas[j].valor +
                            // ' required><label for="uno" class="form-check-label">' +
                            // this.preguntas[i].alternativas[j].respuesta +
                            // '</label><br><input type="radio" class="form-check-input" id="dos" name=respu' +
                            // i +
                            // " value=" +
                            // this.preguntas[i].alternativas[1].valor +
                            // ' required><label for="dos" class="form-check-label">' +
                            // this.preguntas[i].alternativas[1].respuesta +
                            // '</label><br><input type="radio" class="form-check-input" id="tres" name=respu' +
                            // i +
                            // " value=" +
                            // this.preguntas[i].alternativas[2].valor +
                            // ' required><label for="tres" class="form-check-label">' +
                            // this.preguntas[i].alternativas[2].respuesta +
                            // "</label><br>";
                            container2.appendChild(myDiv);
                        }
                        console.log(this.preguntas[i].alternativas);

                        document.getElementById("btn-enviar").hidden = false;
                    }

                }
            }
        },
        calcularNota() {
            let idEs = localStorage.getItem("idEst");
            let sel = document.getElementById("evalua").value;
            //alert(sel);
            //alert(idEs);
            let puntos = 0;
            //alert(this.evaluacionesAux.length);
            // for (let i = 0; i < this.evaluacionesAux.length; i++) {
            //     if (this.evaluacionesAux[i]._id === sel) {
            //         // alert("Entra");
            //         //this.evaluacionesAux[i].realizada = true;
            //         this.modificarEvaluacion(sel, true);
            //     }
            // }

            let totalPreg = 0;
            for (let i = 0; i < this.preguntas.length; i++) {

                if (this.preguntas[i].evaluacionId === sel) {
                    totalPreg++;
                    //alert(this.preguntas.length + "--" + totalPreg);
                    let res = "";
                    let resp = document.getElementsByName("respu" + i);
                    resp.forEach((rp) => {
                        if (rp.checked) {
                            res = rp.value;
                        }
                    });
                    //alert(typeof res);
                    if (res === "true") {
                        puntos = puntos + 1;
                    }
                }
            }
            //alert(totalPreg)
            //alert("Cada punto vale " + (5 / totalPreg).toFixed(1));
            //alert("El total de puntos bien es " + puntos);

            //alert("Su nota es " + (5 / totalPreg * puntos).toFixed(1));
            let notaFinal = ((5 / totalPreg * puntos).toFixed(1));
            //alert("nota: "+ notaFinal)

            let objetoNota = {
                nota: notaFinal,
                estudianteId: idEs,
                evaluacionId: sel
            }
            this.axios.post('/nueva-nota', objetoNota)

            this.mensaje.color = 'success';
            this.mensaje.texto = '!Has realizado la evaluaci√≥n correctamente!';
            this.showAlert()
            document.getElementById('modal').hidden = false
            this.mensaje.notaF = notaFinal;
            let container = document.querySelector("#contenido");
            container.innerHTML = "";
            document.getElementById("btn-enviar").hidden = true;
            this.cargarEva();

        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown;
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs;
        },
    },
};
</script>

<style>
img {
    width: 35px;
}

h2 {
    padding: 35px;
    text-align: center;
}
</style><style>
* {
    text-decoration: none;
    list-style: none;
    margin: 0px;
    padding: 0px;
    outline: none;
}

body {
    margin: 0px;
    padding: 0px;
    font-family: "Open Sans", sans-serif;
}

section {
    width: 100%;
    max-width: 1400px;
    display: table;
    position: relative;
}

/* h1{margin:0px auto; display:table; font-size:26px; padding:40px 0px; color:#002e5b; text-align:center;}
h1 span{font-weight:500;} */

header {
    width: 100%;
    display: table;
    background-color: rgb(13, 65, 47);
    margin-bottom: 20px;
}

nav {
    width: auto;
    float: right;
}

nav ul {
    display: table;
    float: right;
}

nav ul li {
    float: left;
}

nav ul li:last-child {
    padding-right: 0px;
}

nav ul li a {
    color: rgb(167, 241, 215);
    font-size: 16px;
    padding: 18px 20px;
    display: inline-block;
    transition: all 0.5s ease 0s;
}

nav ul li a:hover {
    background-color: rgb(167, 241, 215);
    color: rgb(13, 65, 47);
    transition: all 0.5s ease 0s;
}

nav ul li a:hover i {
    color: #fde428;
    transition: all 0.5s ease 0s;
}

#i {
    text-decoration: none;
}
</style><style lang="scss">
#nav {
    padding: 0px;
    height: 60px;

    a {
        font-weight: bold;

        &.router-link-exact-active {
            color: #5379fa;
        }
    }
}
</style>
