<template>
<div id="app">

    <div id="nav">
        <section>
            <header>
                <nav>
                    <ul>
                        <li>
                            <router-link id="i" to="/grupos">Grupos</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/estudiantes">Estudiantes</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/areas">√Åreas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/evaluacion">Evaluaci√≥n</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/preguntas">Preguntas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/alternativa">Alternativa</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/reporte">Reportes</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/">Salir</router-link>
                        </li>
                        <!-- <li><router-link id="i" to="/registro">Registro</router-link></li> -->
                    </ul>

                </nav>
            </header>

        </section>
    </div>

    <div class="container">

        <h2>Alternativas</h2>

        <b-alert :show="dismissCountDown" dismissible :variant="mensaje.color" @dismissed="dismissCountDown=0" @dismiss-count-down="countDownChanged">
            {{mensaje.texto}}
        </b-alert>

        <form @submit.prevent="editarAlternativa(alternativaEditar)" v-if="editar">
            <h4>Modificar Alternativa</h4>
            <div class="mb-3 row" style="width: 200px">
                <input type="text" class="form-control my-2" name="respuesta" value="" size="11" maxlength="50" placeholder="Respuesta" v-model="alternativaEditar.respuesta" required /><br>
                <!-- <input type="text" class="form-control my-2" name="valor" value="" size="50" maxlength="50" placeholder="Valor" v-model="alternativaEditar.valor" required /><br> -->
                <select v-model="alternativaEditar.valor" class="form-control" id="pregunta">
                    <option disabled selected>¬øEs correcta?</option>
                    <option :value="true">correcta</option>
                    <option :value="false">incorrecta</option>
                </select>
                <select v-model="alternativaEditar.preguntaId" class="form-control" id="pregunta">
                    <option disabled selected>Seleccione una Pregunta</option>
                    <option v-for="pregunta in preguntas" :key="pregunta" :value="pregunta._id">{{pregunta.enunciado}}</option>
                </select>
            </div>
            <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
            <b-button class="my-2" type="submit" @click="editar = false">Cancelar</b-button>
        </form>
        <form @submit.prevent="agregarAlternativa()" hidden="true" v-if="!editar" id="form_1">
            <h4>Agregar Alternativa</h4>
            <div class="mb-3 row" style="width: 200px">
                <input type="text" class="form-control my-2" name="respuesta" value="" size="20" maxlength="50" placeholder="Respuesta" v-model="alternativa.respuesta" required />
                <!-- <input type="text" class="form-control my-2" name="valor" value="" size="50" maxlength="50" placeholder="valor" v-model="alternativa.valor" required /> -->
                <select v-model="alternativa.valor" class="form-control" id="pregunta">
                    <option disabled selected>¬øEs correcta?</option>
                    <option :value="true">correcta</option>
                    <option :value="false">incorrecta</option>
                </select>
                <select v-model="alternativa.preguntaId" class="form-control" id="pregunta">
                    <option disabled selected>Seleccione una Pregunta</option>
                    <option v-for="pregunta in preguntas" :key="pregunta" :value="pregunta._id">{{pregunta.enunciado}}</option>
                </select>
            </div>
            <b-button class="btn-success my-2 btn-block" type="submit">Agregar</b-button>&nbsp;
            <b-button class="my-2" type="button" @click="agregarCancelar()">Cancelar</b-button>

        </form>
        <hr>
       <tr>
            <th>Pregunta:</th> &nbsp;
            <th style="width: 200px;">
                <select v-model="filtroPregunta" class="form-control">
                    <option>Todos</option>
                    <option v-for="pregunta in preguntas" :key="pregunta" :value="pregunta._id">{{pregunta.enunciado}}</option>
                </select>
            </th>&nbsp;

            <th>
                <b-button variant="info" @click="filtroPreguntas()" type="submit">Filtrar</b-button>
            </th>
        </tr>

        <br>

        <table class="table  table table-primary table-striped">
            <thead>
                <tr class="table-secondary">
                    <th scope="col">id</th>
                    <th scope="col">Respuesta</th>
                    <th scope="col">Valor</th>
                    <th scope="col">Pregunta</th>
                    <th scope="col">
                         <a href="#" @click="agregar()" title="Adicionar">
                            <img src="../assets/a√±adir.png" />
                        </a>
                        <!-- <b-button class="btn-success my-2 btn-block" title="Adicionar" type="button" @click="agregar()">
                            <img src="../assets/a√±adir.png" @click="agregar()" />
                        </b-button> -->
                    </th>

                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in alternativas" :key="index">
                    <th scope="row">{{ item._id }}</th>
                    <td>{{ item.respuesta }}</td>
                    <td>{{item.valor}}</td>
                    <td>{{item.preguntaId}}</td>
                    <td>
                        <!-- <b-button @click="alerta()">Acci√≥n</b-button> -->
                        <b-button @click="eliminarAlternativa(item._id)" class="btn-danger btn-sm mx-2">Eliminar
                        </b-button>
                        <b-button @click="activarEdicion(item._id)" class="btn-warning btn-sm">Editar
                        </b-button>
                    </td>

                </tr>

            </tbody>
        </table>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            alternativas: [],
            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {
                color: "",
                texto: ""
            },
            alternativa: {
                respuesta: "",
                valor: false,
                preguntaId: ""
            },
            preguntas: [],
            pregunta: {
                enunciado: ""

            },
            editar: false,
            alternativaEditar: {},
            filtroPregunta: "Todos"
        };
    },
    created() {
        this.listarAlternativas();
        this.listarPreguntas();
    },
    methods: {
        filtroPreguntas() {
            var criterio2 = this.filtroPregunta

            if (criterio2 === "Todos") {

                this.axios.get('/alternativa')
                    .then((response) => {
                        this.alternativas = response.data;
                    })
                    .catch((e) => {
                        console.log('error' + e);
                    })

            } else {

                this.axios.get(`/filtroU/${criterio2}`)

                    .then((response) => {
                        console.log(response.data)
                        this.alternativas = response.data;
                    })
                    .catch((e) => {
                        console.log('error' + e);
                    })

            }

        },
        listarPreguntas() {
            this.axios.get('/pregunta')
                .then(res => {
                    console.log(res.data);
                    this.preguntas = res.data;
                    console.log(preguntas);
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },
        listarAlternativas() {
            this.axios.get('/alternativa')
                .then(res => {
                    console.log(res.data);
                    this.alternativas = res.data;
                    console.log(alternativas);
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },
        agregar() {
            document.getElementById('form_1').hidden = false

        },
        agregarCancelar() {
            document.getElementById('form_1').hidden = true
            this.alternativa.respuesta = '';
            this.alternativa.valor = false;
            this.alternativa.preguntaId = '';
        },
        agregarAlternativa() {
            this.axios.post('/nueva-alternativa', this.alternativa)
                .then(res => {
                    this.alternativas.push(res.data)
                    this.alternativa.respuesta = '';
                    this.alternativa.valor = false;
                    this.alternativa.preguntaId = '';

                    this.mensaje.color = 'success';
                    this.mensaje.texto = 'Alternativa Agregada!';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true
                })
                .catch(e => {
                    console.log(e.response);
                    if (e.response.data.error.errors.nombre.message) {
                        this.mensaje.texto = e.response.data.error.errors.nombre.message
                    } else {
                        this.mensaje.texto = 'Error de sistema';
                    }
                    this.mensaje.color = 'danger';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true

                })

        },
        eliminarAlternativa(id) {
            console.log(id);
            this.axios.delete(`/alternativa/${id}`)
                .then(res => {
                    const index = this.alternativas.findIndex(item => item._id === res.data._id);
                    this.alternativas.splice(index, 1);
                    this.mensaje.color = 'danger';
                    this.mensaje.texto = 'alternativa Eliminada';
                    this.showAlert();
                })
                .catch(e => {
                    console.log(e.response);
                })

        },
        activarEdicion(id) {
            this.editar = true;
            console.log(id);
            this.axios.get(`/alternativa/${id}`)
                .then(res => {
                    this.alternativaEditar = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        editarAlternativa(item) {
            this.axios.put(`/alternativa/${item._id}`, item)
                .then(res => {
                    const index = this.alternativas.findIndex(n => n._id === res.data._id);
                    this.alternativas[index].respuesta = res.data.respuesta;
                    this.alternativas[index].valor = res.data.valor;
                    this.alternativas[index].preguntaId = res.data.preguntaId;

                    this.mensaje.color = 'success';
                    this.mensaje.texto = 'Alternativa Editada';
                    this.showAlert();
                    this.editar = false;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs
        }
    },
}
</script>

<style>
img {
    width: 35px;
}

h2 {
    padding: 35px;
    text-align: center;
}
</style><style>
* {
    text-decoration: none;
    list-style: none;
    margin: 0px;
    padding: 0px;
    outline: none;
}

body {
    margin: 0px;
    padding: 0px;
    font-family: 'Open Sans', sans-serif;
}

section {
    width: 100%;
    max-width: 1400px;
    display: table;
    position: relative;
}

/* h1{margin:0px auto; display:table; font-size:26px; padding:40px 0px; color:#002e5b; text-align:center;}
h1 span{font-weight:500;} */

header {
    width: 100%;
    display: table;
    background-color: rgb(13, 65, 47);
    margin-bottom: 20px;
}

nav {
    width: auto;
    float: right;
}

nav ul {
    display: table;
    float: right;
}

nav ul li {
    float: left;
}

nav ul li:last-child {
    padding-right: 0px;
}

nav ul li a {
    color: rgb(167, 241, 215);
    font-size: 16px;
    padding: 18px 20px;
    display: inline-block;
    transition: all 0.5s ease 0s;
}

nav ul li a:hover {
    background-color: rgb(167, 241, 215);
    color: rgb(13, 65, 47);
    transition: all 0.5s ease 0s;
}

nav ul li a:hover i {
    color: #fde428;
    transition: all 0.5s ease 0s;
}

#i {
    text-decoration: none;
}
</style><style lang="scss">
#nav {
    padding: 0px;
    height: 60px;

    a {
        font-weight: bold;

        &.router-link-exact-active {
            color: #5379fa;

        }
    }
}
</style>
