<template>
<div id="app">
    
    <div id="nav">
        <section>
            <header>
                <nav>
                    <ul>
                        <li>
                            <router-link id="i" to="/evaluacionE">Evaluacion</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/resultado">Resultados</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/">Salir</router-link>
                        </li>

                        <!-- <li><router-link id="i" to="/registro">Registro</router-link></li> -->
                    </ul>
                </nav>
            </header>
        </section>
    </div>
    <div class="container">
        <h2>Reportes</h2>

        <!-- <tr>
            <th>Evaluacion:</th> &nbsp;
            <th style="width: 200px;">
                <select v-model="filtroEvaluacion" class="form-control">
                    <option>Todos</option>
                    <option v-for="evaluacion in evaluaciones" :key="evaluacion" :value="evaluacion._id">{{evaluacion.nombre}}</option>
                </select>
            </th>&nbsp;

            <th>
                <b-button variant="info" @click="filtroEvaluaciones()" type="submit">Filtrar</b-button>
            </th>
        </tr> -->
        <br>
        <table class="table  table table-primary table-striped">
            <thead>
                <tr class="table-secondary">
                    <th scope="col">Evaluacion</th>
                    <!-- <th scope="col">Estudiante</th> -->
                    <th scope="col">Nota</th>

                </tr>
            </thead>
            <tbody id="tblBody">
                <tr v-for="(item, index) in tabla" :key="index">
                    <th scope="row">{{ item.evaluacionId}}</th>
                    <!-- <td>{{ item.estudianteId }}</td> -->
                    <td>{{item.nota}}</td>
                </tr>

            </tbody>
            </table>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            evaluaciones: [],
            estudiantes:[],
            notas:[],
            tabla:[],
            nota:{
                nota: 0,
                estudianteId: "",
                evaluacionId: ""
            },
            evaluacion: {
                nombre: ""

            },
            filtroEvaluacion: "Todos"

        }
    },
    created() {
        setTimeout(() => {
            this.cargarTabla();
        }, 500)
        this.listarEvaluaciones();
        this.listarNotas();
        // this.listarEstudiantes();
    },
    methods: {
       
        cargarTabla(idEs=0, idEv=0, opt=0){
            let idEst = localStorage.getItem("idEst");
            for (let i = 0; i < this.notas.length; i++) {
                // for (let a = 0; a < this.estudiantes.length; a++) {
                   for (let j = 0; j < this.evaluaciones.length; j++) {
                       if(this.notas[i].estudianteId===idEst && this.notas[i].evaluacionId===this.evaluaciones[j]._id){
                           this.tabla.push({evaluacionId: this.evaluaciones[j].nombre,
                                        // estudianteId: this.estudiantes[a].nombre,
                                        nota: this.notas[i].nota})

                       }
                       
                   //}
                    
                }
                
            }

        },
        
        listarEstudiantes() {
            this.axios.get('/estudiante')
                .then(res => {
                    console.log(res.data);
                    this.estudiantes = res.data;
                   
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },

    
        filtroEvaluaciones() {
            var criterio2 = this.filtroEvaluacion

            if (criterio2 === "Todos") {

                this.axios.get('/notas')
                    .then((response) => {
                        this.notas = response.data;
                        this.tabla=[];
                        this.cargarTabla();
                        
                    })
                    .catch((e) => {
                        console.log('error' + e);
                    })

            } else {

                this.axios.get(`/filtroN/${criterio2}`)

                    .then((response) => {
                        console.log(response.data)
                        this.notas = response.data;
                        this.tabla=[];
                        this.cargarTabla();
                    })
                    .catch((e) => {
                        console.log('error' + e);
                    })

            }
            

        },
        listarEvaluaciones() {
            this.axios.get('/evaluacion')
                .then(res => {
                    console.log(res.data);
                    this.evaluaciones = res.data;
                    console.log(evaluaciones);
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },
        listarNotas() {
            this.axios.get('/notas')
                .then(res => {
                    console.log(res.data);
                    this.notas = res.data;
                    console.log(notas);
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },

    },
}
</script>

