<template>
<div id="app">
    <div id="nav">
        <section>
            <header>
                <nav>
                    <ul>
                        <li>
                            <router-link id="i" to="/grupos">Grupos</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/estudiantes">Estudiantes</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/areas">√Åreas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/evaluacion">Evaluaci√≥n</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/preguntas">Preguntas</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/alternativa">Alternativa</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/reporte">Reportes</router-link>
                        </li>
                        <li>
                            <router-link id="i" to="/">Salir</router-link>
                        </li>
                        <!-- <li><router-link id="i" to="/registro">Registro</router-link></li> -->
                    </ul>

                </nav>
            </header>

        </section>
    </div>

    <div class="container">
        <h2>Evaluaciones</h2>
        <b-alert :show="dismissCountDown" dismissible :variant="mensaje.color" @dismissed="dismissCountDown=0" @dismiss-count-down="countDownChanged">
            {{mensaje.texto}}
        </b-alert>

        <form @submit.prevent="editarEval(evaluacionEditar)" v-if="editar">
            <h4>Modificar Evaluaci√≥n</h4>
            <div class="mb-3 row" style="width: 200px">
                <input type="text" class="form-control my-2" name="nombre" value="" size="11" maxlength="20" placeholder="Nombre" v-model="evaluacionEditar.nombre" required /><br>
                <input type="text" class="form-control my-2" name="descripcion" value="" size="50" maxlength="50" placeholder="Descripcion" v-model="evaluacionEditar.descripcion" required /><br>
                <!-- <input type="date" class="form-control my-2" name="fecha" value="" size="50" maxlength="50" placeholder="Fecha" required /><br> -->
                <select v-model="evaluacionEditar.areaId" class="form-control" id="eval">
                    <option v-for="area in areas" :key="area" :value="area._id">{{area.nombre}}</option>
                </select>
                <select v-model="evaluacionEditar.grupoId" class="form-control" id="eval">
                    <option v-for="grupo in grupos" :key="grupo" :value="grupo._id">{{grupo.nombre}}</option>
                </select>
            </div>
            <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
            <b-button class="my-2" type="submit" @click="editar = false">Cancelar</b-button>

        </form>

        <form @submit.prevent="agregarEval()" v-if="!editar" hidden="true" id="form_1">
            <h4>Agregar Evaluaci√≥n</h4>
            <div class="mb-3 row" style="width: 200px">
                <input type="text" class="form-control my-2" name="nombre" value="" size="11" maxlength="20" placeholder="Nombre" v-model="evaluacion.nombre" required />
                <input type="text" class="form-control my-2" name="descripcion" value="" size="50" maxlength="50" placeholder="Descripcion" v-model="evaluacion.descripcion" required />
                <!-- <input type="date" class="form-control my-2" name="fecha" value="" size="50" maxlength="50" placeholder="Fecha" required /> -->
                <select v-model="evaluacion.areaId" class="form-control" id="area">
                    <option disabled selected>Seleccione una √°rea</option>
                    <option v-for="area in areas" :key="area" :value="area._id">{{area.nombre}}</option>
                </select>
                <select v-model="evaluacion.grupoId" class="form-control" id="grupo">
                    <option disabled selected>Seleccione un Grupo</option>
                    <option v-for="grupo in grupos" :key="grupo" :value="grupo._id">{{grupo.nombre}}</option>
                </select>

            </div>
            <b-button class="btn-success my-2 btn-block" type="submit">Agregar</b-button>&nbsp;
            <b-button class="my-2" type="button" @click="agregarCancelar()">Cancelar</b-button>

        </form>
        <hr>
        <div>
            <tr>
                <th>Grupo:</th> &nbsp;
                <th style="width: 150px">
                    <select v-model="filtroGrupo" class="form-control">
                        <option>Todos</option>
                        <option v-for="grupo in grupos" :key="grupo" :value="grupo._id">{{grupo.nombre}}</option>
                    </select>
                </th>&nbsp;
                <th>√Årea:</th>&nbsp;
                <th>
                    <select v-model="filtroArea" class="form-control">
                        <option>Todos</option>
                        <option v-for="area in areas" :key="area" :value="area._id">{{area.nombre}}</option>
                    </select>
                </th>&nbsp;
                <th>
                    <b-button variant="info" class="my-2" color="success" elevation="19" @click="filtro()" type="submit">Filtrar</b-button>
                </th>
            </tr>
        </div>

        

        <br>

        <table class="table  table table-primary table-striped">
            <thead>
                <tr class="table-secondary">
                    <th scope="col">C√≥digo Evaluaci√≥n</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Descripci√≥n</th>
                    <th scope="col">Fecha</th>
                    <th scope="col">
                        <a href="#" @click="agregar()" title="Adicionar">
                            <img src="../assets/a√±adir.png" />
                        </a>
                        <!-- <b-button class="" title="Adicionar" type="button" @click="agregar()">
                            <img src="../assets/a√±adir.png" />
                        </b-button> -->
                    </th>

                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in evaluaciones" :key="index">
                    <th scope="row">{{ item._id }}</th>
                    <td>{{ item.nombre }}</td>
                    <td>{{item.descripcion}}</td>
                    <td>{{item.fecha}}</td>
                    <td>
                        <!-- <b-button @click="alerta()">Acci√≥n</b-button> -->
                        <b-button @click="eliminarEval(item._id)" class="btn-danger btn-sm mx-2">Eliminar
                        </b-button>
                        <b-button @click="activarEdicion(item._id)" class="btn-warning btn-sm">Editar
                        </b-button>
                    </td>

                </tr>

            </tbody>
        </table>

    </div>

    <router-view />

</div>
</template>

<script>
export default {
    data() {
        return {
            evaluaciones: [],
            evaluaciones2: [],

            dismissSecs: 5,
            dismissCountDown: 0,
            mensaje: {
                color: "",
                texto: ""
            },
            evaluacion: {
                nombre: "",
                descripcion: "",
                areaId: "",
                grupoId: ""
            },
            grupos: [],

            grupo: {
                nombreG: ""

            },
            areas: [],

            area: {
                nombreH: ""

            },
            editar: false,
            evaluacionEditar: {},
            filtroGrupo: "Todos",
            filtroArea: "Todos",

        };
    },
    created() {
        this.listarEvaluaciones();
        this.listarGrupos();
        this.listarAreas();
    },
    methods: {

        filtro() {
            var criterio2 = this.filtroArea;
            var criterio = this.filtroGrupo;

            if (criterio2 === "Todos" && criterio == "Todos") {

                this.axios.get('/evaluacion')
                    .then((response) => {
                        // console.log(response.data)
                        this.evaluaciones = response.data;
                    })
                    .catch((e) => {
                        console.log('error' + e);
                    })

                //this.evaluaciones = "";
            } else {

                if (criterio === "Todos") {
                    this.axios.get(`/filtroA/${criterio2}`)

                        .then((response) => {
                            console.log(response.data)

                            this.evaluaciones = response.data;
                        })
                        .catch((e) => {
                            console.log('error' + e);
                        })
                    //this.evaluaciones = "";

                } else if (criterio2 === "Todos") {
                    this.axios.get(`/filtroG/${criterio}`)

                        .then((response) => {
                            console.log(response.data)

                            this.evaluaciones = response.data;
                        })
                        .catch((e) => {
                            console.log('error' + e);
                        })

                    //this.evaluaciones = "";

                } else {

                    this.axios.get(`/filtroE/${criterio}/${criterio2}`)

                        .then((response) => {
                            console.log(response.data)

                            this.evaluaciones = response.data;
                        })
                        .catch((e) => {
                            console.log('error' + e);
                        })
                    //this.evaluaciones = "";

                }

            }
            //this.evaluaciones = "";

        },

        listarGrupos() {
            this.axios.get('/grupo')
                .then(res => {
                    console.log(res.data);
                    this.grupos = res.data;
                    console.log(grupos);
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },
        listarAreas() {
            this.axios.get('/area')
                .then(res => {
                    console.log(res.data);
                    this.areas = res.data;
                    console.log(areas);
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },

        listarEvaluaciones() {
            this.axios.get('/evaluacion')
                .then(res => {
                    console.log(res.data);
                    this.evaluaciones = res.data;
                })
                .catch(e => {
                    console.log(e.responsive);
                })
        },
        agregar() {
            document.getElementById('form_1').hidden = false

        },
        agregarCancelar() {
            document.getElementById('form_1').hidden = true
            this.evaluacion.nombre = '';
            this.evaluacion.descripcion = '';
            this.evaluacion.areaId = '';
            this.evaluacion.grupoId = '';
        },

        agregarEval() {
            this.axios.post('/nueva-evaluacion', this.evaluacion)
                .then(res => {
                    this.evaluaciones.push(res.data)
                    this.evaluacion.nombre = '';
                    this.evaluacion.descripcion = '';
                    this.evaluacion.areaId = '';
                    this.evaluacion.grupoId = '';

                    this.mensaje.color = 'success';
                    this.mensaje.texto = 'Evaluacion Agregado!';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true
                })
                .catch(e => {
                    console.log(e.response);
                    if (e.response.data.error.errors.nombre.message) {
                        this.mensaje.texto = e.response.data.error.errors.nombre.message
                    } else {
                        this.mensaje.texto = 'Error de sistema';
                    }
                    this.mensaje.color = 'danger';
                    this.showAlert()
                    document.getElementById('form_1').hidden = true

                })

        },
        eliminarEval(id) {
            console.log(id);
            this.axios.delete(`/evaluacion/${id}`)
                .then(res => {
                    const index = this.evaluaciones.findIndex(item => item._id === res.data._id);
                    this.evaluaciones.splice(index, 1);
                    this.mensaje.color = 'danger';
                    this.mensaje.texto = 'Evaluacion Eliminada';
                    this.showAlert();
                })
                .catch(e => {
                    console.log(e.response);
                })

        },
        activarEdicion(id) {
            this.editar = true;
            console.log(id);
            this.axios.get(`/evaluacion/${id}`)
                .then(res => {
                    this.evaluacionEditar = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        // editarEval(item) {
        //     this.axios.put(`/evaluacion/${item._id}`, item)
        //         .then(res => {
        //             const index = this.evaluaciones.findIndex(n => n._id === res.data._id);
        //             this.evaluaciones[index].nombre = res.data.nombre;
        //             this.evaluaciones[index].descripcion = res.data.descripcion;
        //             this.evaluaciones[index].areaId = res.data.areaId;
        //             this.evaluaciones[index].grupoId = res.data.grupoId;
        //             this.mensaje.color = 'success';
        //             this.mensaje.texto = 'Evaluaci√≥n Editada';
        //             this.showAlert();
        //             this.editar = false;
        //         })
        //         .catch(e => {
        //             console.log(e.response);
        //         })
        // },
        editarEval(item) {
            item = {
                areaId: item.areaId,
                nombre: item.nombre,
                realizada: item.realizada,
                fecha: item.fecha,
                _id: item._id,
                descripcion: item.descripcion,
                grupoId: item.grupoId
            }
            this.axios.put(`/evaluacion/${item._id}`, item)
                .then(res => {
                    // const index = this.evaluaciones.findIndex(n => n._id === res.data._id);
                    // this.evaluaciones[index].nombre = res.data.nombre;
                    // this.evaluaciones[index].descripcion = res.data.descripcion;
                    // this.evaluaciones[index].areaId = res.data.areaId;
                    // this.evaluaciones[index].grupoId = res.data.grupoId;
                    this.mensaje.color = 'success';
                    this.mensaje.texto = 'Evaluaci√≥n Editada';
                    this.showAlert();
                    this.editar = false;
                })
                .catch(e => {
                    console.log(e.response);
                })
        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs
        }
    },
}
</script>

<style>
img {
    width: 35px;
}

h2 {
    padding: 35px;
    text-align: center;
}
</style><style>
* {
    text-decoration: none;
    list-style: none;
    margin: 0px;
    padding: 0px;
    outline: none;
}

body {
    margin: 0px;
    padding: 0px;
    font-family: 'Open Sans', sans-serif;
}

section {
    width: 100%;
    max-width: 1400px;
    display: table;
    position: relative;
}

/* h1{margin:0px auto; display:table; font-size:26px; padding:40px 0px; color:#002e5b; text-align:center;}
h1 span{font-weight:500;} */

header {
    width: 100%;
    display: table;
    background-color: rgb(13, 65, 47);
    margin-bottom: 20px;
}

nav {
    width: auto;
    float: right;
}

nav ul {
    display: table;
    float: right;
}

nav ul li {
    float: left;
}

nav ul li:last-child {
    padding-right: 0px;
}

nav ul li a {
    color: rgb(167, 241, 215);
    font-size: 16px;
    padding: 18px 20px;
    display: inline-block;
    transition: all 0.5s ease 0s;
}

nav ul li a:hover {
    background-color: rgb(167, 241, 215);
    color: rgb(13, 65, 47);
    transition: all 0.5s ease 0s;
}

nav ul li a:hover i {
    color: #fde428;
    transition: all 0.5s ease 0s;
}

#i {
    text-decoration: none;
}
</style><style lang="scss">
#nav {
    padding: 0px;
    height: 60px;

    a {
        font-weight: bold;

        &.router-link-exact-active {
            color: #5379fa;

        }
    }
}
</style>
